<template>
	<view class="user-businessin">
		<view class="inpt-wrap row verCenter">
			<text class="label">店铺名称</text>
			<input type="text" placeholder="店铺名称" v-model="shop_name" />
		</view>
		<view class="inpt-wrap row verCenter">
			<text class="label">联系人</text>
			<input type="text" v-model="author" placeholder="联系人" />
		</view>
		<view class="inpt-wrap row verCenter">
			<text class="label">联系方式</text>
			<input type="text" v-model="mobile" placeholder="联系方式" />
		</view>
		<!-- 默认线下先隐藏 -->
		<!-- <view class="inpt-wrap row verCenter">
			<text class="label">商家类型</text>
			<picker @change="bindPickerChange" :value="index" :range="array">
				<view class="chosetype">{{ condition===-1?"请选择":array[index] }}</view>
			</picker>
		</view> -->
		<view class="inpt-wrap row verCenter">
			<text class="label">商家类型</text>
			<view class="chosetype">线下</view>
		</view>
		<view class="inpt-wrap row verCenter" v-if="categorylist[index1]">
			<text class="label">经营类目</text>
			<picker @change="bindPickerChange1" :value="index1" :range="categorylist" range-key="category_name">
				<view class="chosetype">{{ condition === -1 ? categorylist[index1].category_name || '请选择' : categorylist[index1].category_name }}</view>
			</picker>
		</view>
		<view class="inpt-wrap row verCenter" >
			<text class="label">商家地址</text>
			<picker mode="multiSelector" @change="bindMultiPickerChange" @columnchange="bindMultiPickerColumnChange" :value="multiIndex" :range="multiArray" range-key="name">
				<view class="options" style="white-space: nowrap;" @click="getlist">
					<!-- <text style="margin-right: 15rpx;">{{ condition === -1 ? multiArray[0][multiIndex[0]].name || '请选择' : multiArray[0][multiIndex[0]].name }}</text>
					<text style="margin-right: 15rpx;">{{ condition === -1 ? multiArray[1][multiIndex[1]].name || '' : multiArray[1][multiIndex[1]].name }}</text>
					<text>{{ condition === -1 ? multiArray[2][multiIndex[2]].name || '' : multiArray[2][multiIndex[2]].name }}</text> -->
					
				  <!-- #ifdef H5 || MP-WEIXIN -->
				    <text class="proleft" >{{ condition === -1 ? multiArray[0][multiIndex[0]].name || '请选择' : multiArray[0][multiIndex[0]].name }}</text>
				    <text class="proleft" >{{ condition === -1 ? multiArray[1][multiIndex[1]].name || '' : multiArray[1][multiIndex[1]].name }}</text>
				    <text >{{ condition === -1 ? multiArray[2][multiIndex[2]].name || '' : multiArray[2][multiIndex[2]].name }}</text>
				    <!-- #endif -->
					<!-- #ifdef APP-PLUS -->
					 <text class="proleft" v-if="condition === -1">{{multiArray[0][multiIndex[0]]==undefined?  '请选择': multiArray[0][multiIndex[0]].name }}</text>
					 <text class="proleft" v-if="condition !== -1">{{ multiArray[0][multiIndex[0]].name }}</text>
					  <text  class="proleft" v-if="condition === -1">{{multiArray[1][multiIndex[1]]==undefined?  '': multiArray[1][multiIndex[1]].name }}</text>
					  <text class="proleft" v-if="condition !== -1">{{ multiArray[1][multiIndex[1]].name }}</text> 
					  <text class="proleft" v-if="condition === -1">{{multiArray[2][multiIndex[2]]==undefined?  '': multiArray[2][multiIndex[2]].name }}</text>
					  <text class="proleft" v-if="condition !== -1">{{ multiArray[2][multiIndex[2]].name }}</text> 
					
				   <!-- #endif -->
				</view>
			</picker>
		</view>
		<view class="inpt-wrap row verCenter">
			<text class="label">详细地址</text>
			<input type="text" v-model="address" placeholder="请填写详细地址" />
		</view>
		<view class="inpt-wrap row verCenter">
			<text class="label">通联商户账号</text>
			<input type="text" v-model="tonglian_account" placeholder="请填写通联商户账号" />
		</view>
		<view class="inpt-wrap row verCenter" style="border-bottom:none">
			<text class="label">邮箱</text>
			<input type="text" v-model="email" placeholder="请填写邮箱" />
		</view>
		<!-- <view class="inpt-wrap row verCenter">
			<text class="label">身份证</text>
			<input type="text" v-model="id_card" placeholder="请填写身份证" />
		</view> -->
		<view class="text-area">
			<textarea maxlength="50" class="boxsiz" auto-height placeholder="店铺简介..." placeholder-style="color:#BBBBBB" v-model="shop_intro" @input="numchange"></textarea>
			<text class="num">{{ num }}/50</text>
		</view>
		<!-- 上传图片先隐藏有需要再用 -->
		<!-- <view class="business-upload row bothSide">
			<view class="l">
				<image src="https://golemo.oss-cn-shenzhen.aliyuncs.com//5f3281a2eccc7.png" mode="aspectFill"></image>
			</view>
			<view class="r">
				<image src="https://golemo.oss-cn-shenzhen.aliyuncs.com//5f3281bb12ab9.png" mode="aspectFill"></image>
			</view>
		</view>
		<view class="shop-upload">
			<text class="title">店铺图标</text>
			<view class="list row">
				<view class="box">
					<image src="https://golemo.oss-cn-shenzhen.aliyuncs.com//5f3282ae7c62d.png" mode="aspectFill"></image>
				</view>
				<view class="box">
					<image src="https://golemo.oss-cn-shenzhen.aliyuncs.com//5f3246ffad6d5.jpg" mode="aspectFill"></image>
				</view>
				<view class="box">
					<image src="https://golemo.oss-cn-shenzhen.aliyuncs.com//5f3246ffad6d5.jpg" mode="aspectFill"></image>
				</view>
			</view>
		</view> -->
		<view class="agreement row verCenter">
			<icon class="ico-1" v-show="!icoShow" @click="icoShow = true"></icon>
			<icon class="ico-2" v-show="icoShow" @click="icoShow = false"></icon>
			<text class="t1">同意并认真阅读</text>
			<navigator url="/pages/user/agreement"><text class="t2">《商家入驻规则协议》</text></navigator>
		</view>
		<view class="btn" @click="submit">商家入驻</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';
import { myreg, checkemail, checkId_card, getMyShop } from '@/util/util.js';
export default {
	data() {
		return {
			array: ['线上', '线下'],
			index: 1,
			categorylist: [],
			category_typeId: '',
			category: '',
			categoryId: '',
			index1: 0,
			shop_name: '',
			shop_intro: '',
			author: '',
			mobile: '',
			province: '',
			provinceId: '',
			city: '',
			district: '',
			districtId: '',
			tonglian_account: '',
			email: '',
			multiArray: [[], [], []],
			multiIndex: [0, 0, 0],
			province: [],
			city: [],
			area: [],
			proId: '',
			cityId: '',
			areaId: '',
			address: '',
			num: 0,
			condition: '',
			icoShow: true
		};
	},
	onload(){
		
	},
	onShow() {
		this.getData()
	},
	
	methods: {
		// 获取信息
		getData(){
			this.request(API.getMyShop, 'GET').then(res => {
					console.log(res); //获取数据
					if (res.code == 10000) {
						var _data = res.data;
						this.condition = _data.status;
						if (this.condition === 0 || this.condition === 2 || this.condition === 1) {
							this.shop_name = _data.shop_name;
							this.category_typeId = _data.category_type;
							this.categoryId = _data.category;
							this.author = _data.author;
							this.mobile = _data.mobile;
							this.provinceId = _data.province;
							this.cityId = _data.city;
							this.districtId = _data.district;
							this.address = _data.address;
							this.tonglian_account = _data.tonglian_account;
							this.email = _data.email;
							// this.id_card = _data.id_card;
							this.shop_intro = _data.shop_intro;
							//经营类目的id获取索引  商家类型
							this.index = _data.category_type;
							console.log(this.index);
							console.log(this.index === '' ? '请选择' : this.array[this.index]);
							console.log(this.array[this.index]);
							//this.$set(this.array,0,this.index)
							this.request(API.getCategory, 'post', {
								// type: this.category_typeId
								type: 1
							}).then(res => {
								if (res.code == 10000) {
									this.categorylist = res.data;
									this.index1 = this.categorylist.findIndex(item => item.category_id == this.categoryId);
									console.log(this.categoryId);
									console.log(this.index1);
									console.log(this.categorylist[this.index1].category_name + '得到结果');
								} else {
									console.log('失败' + res);
								}
							});
			
							//开始获取省市区
							this.request(API.getRegion, 'POST', {
								id: 0
							}).then(res => {
								if (res.code == 10000) {
									this.province = res.data;
									this.$set(this.multiArray, 0, this.province);
									var proIndex = this.provinceId - 1;
									this.multiIndex.splice(0, 1, proIndex);
									this.$set(this.multiIndex, 0, proIndex); //获得省名
								}
							});
							this.request(API.getRegion, 'POST', {
								id: this.provinceId
							}).then(res => {
								if (res.code == 10000) {
									this.city = res.data;
									var cityIndex = this.city.findIndex(item => item.id == this.cityId);
									console.log(cityIndex);
									this.$set(this.multiArray, 1, this.city);
									this.multiIndex.splice(1, 1, cityIndex);
									this.$set(this.multiIndex, 1, cityIndex); //获得市名
									//默认选取地区
								}
							});
							this.request(API.getRegion, 'POST', {
								id: this.cityId
							}).then(res => {
								if (res.code == 10000) {
									this.area = res.data;
									this.$set(this.multiArray, 2, this.area);
									var areaIndex = this.area.findIndex(item => item.id == this.districtId);
									console.log(areaIndex);
									this.multiIndex.splice(2, 1, areaIndex);
									this.$set(this.multiIndex, 2, areaIndex); //获得市名
								}
							});
							//结束
						}
						//无数据返回状态
					} else if (res.code === 10002) {
						this.condition = -1;
						this.request(API.getCategory, 'post', {
							// type: this.category_typeId
							type: 1
						}).then(res => {
							if (res.code == 10000) {
								this.categorylist = res.data;
								this.categoryId = this.categorylist[this.index1].category_id;
							} else {
								console.log('失败' + res);
							}
						});
					}
				});
			
		},
		getProvince() {
			//城市三级联动接口  id=0选取所有省
			this.request(API.getRegion, 'POST', {
				id: 0
			}).then(res => {
				if (res.code == 10000) {
					this.province = res.data;
					this.$set(this.multiArray, 0, this.province);
					this.getCity(res.data[0].id); //默认选取城市
				}
			});
		},
		getCity(proviceId) {
			this.request(API.getRegion, 'POST', {
				id: proviceId
			}).then(res => {
				if (res.code == 10000) {
					this.city = res.data;
					this.$set(this.multiArray, 1, this.city);
					this.getArea(res.data[0].id); //默认选取地区
				}
			});
		},
		getArea(cityId) {
			this.request(API.getRegion, 'POST', {
				id: cityId
			}).then(res => {
				if (res.code == 10000) {
					this.area = res.data;
					this.$set(this.multiArray, 2, this.area);
				}
			});
		},
		//点击获取省市区
		getlist() {
			if (this.condition === -1) {
				this.getProvince();
			}
		},
		//选择改变获取省市区id
		bindMultiPickerChange(e) {
			console.log('picker发送选择改变，携带值为', e.detail.value);

			this.multiIndex = e.detail.value;

			console.log(this.multiArray, this.multiIndex);
			this.provinceId = this.province[e.detail.value[0]].id;
			this.cityId = this.city[e.detail.value[1]].id;
			this.districtId = this.area[e.detail.value[2]].id;
			console.log(this.provinceId, this.cityId, this.districtId);
		},
		//选择列改变后续省市区
		bindMultiPickerColumnChange(e) {
			console.log('修改的列为', e.detail.column, '，值为', e.detail.value);

			//this.multiIndex[e.detail.column] = e.detail.value;

			if (e.detail.column === 0) {
				this.getCity(this.province[e.detail.value].id);

				this.$set(this.multiIndex, 0, e.detail.value);
				this.multiIndex.splice(1, 1, 0);
				this.multiIndex.splice(2, 1, 0);
			} else if (e.detail.column === 1) {
				this.getArea(this.city[e.detail.value].id);

				this.$set(this.multiIndex, 1, e.detail.value);
				this.multiIndex.splice(2, 1, 0);
			} else if (e.detail.column === 2) {
				this.$set(this.multiIndex, 2, e.detail.value);
			}
		},
		bindPickerChange(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.index = e.target.value;
			this.category_typeId = e.target.value;
			this.request(API.getCategory, 'post', {
				type: this.category_typeId
			}).then(res => {
				if (res.code == 10000) {
					console.log(res);
					this.categorylist = res.data;
				} else {
					console.log('失败' + res);
				}
			});
		},
		bindPickerChange1(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.index1 = e.target.value;
			this.categoryId = this.categorylist[this.index1].category_id;
		},
		numchange() {
			var len = this.shop_intro.length;
			this.num = 50 - len;
		},

		//提交入驻资料
		submit() {
			if (this.condition === -1 || this.condition === 1) {
				if (this.shop_name == '') {
					uni.showToast({
						title: '请填写商家名称',
						duration: 2000,
						icon: 'none'
					});
					return;
				}
				if (this.author == '') {
					uni.showToast({
						title: '请填写联系人',
						duration: 2000
					});
					return;
				}
				if (this.mobile == '') {
					uni.showToast({
						title: '请填写联系方式',
						duration: 2000
					});
					return;
				}
				if (this.categoryId == '' || this.categoryId == undefined) {
					uni.showToast({
						title: '请填写经营类目',
						duration: 2000
					});
					return;
				}
				if (this.address == '') {
					uni.showToast({
						title: '请填写详细地址',
						duration: 2000
					});
					return;
				}
				if (this.tonglian_account == '') {
					uni.showToast({
						title: '请填写通联商账户详细地址',
						duration: 2000
					});
					return;
				}
				if (this.email == '') {
					uni.showToast({
						title: '请填写邮箱',
						duration: 2000
					});
					return;
				}
				// if (this.id_card == "") {
				// 	uni.showToast({
				// 		title: '请填写身份证',
				// 		duration: 2000
				// 	});
				// 	return;
				// }
				if (this.shop_intro == '') {
					uni.showToast({
						title: '请填写商家简介',
						duration: 2000
					});
					return;
				}
				if (!myreg.test(this.mobile)) {
					uni.showToast({
						title: '号码不正确',
						duration: 2000
					});
					return;
				}
				if (!checkemail.test(this.email)) {
					uni.showToast({
						title: '邮箱不正确',
						duration: 2000
					});
					return;
				}
				// if (!checkId_card.test(this.id_card)) {
				// 	uni.showToast({
				// 		title: '身份证不正确',
				// 		duration: 2000
				// 	});
				// 	return;
				// }
				if (this.icoShow == false) {
					uni.showToast({
						title: '请同意入驻协议',
						duration: 2000
					});
					return;
				}

				this.request(API.addShop, 'POST', {
					shop_name: this.shop_name,
					category_type: 1,
					category: this.categoryId,
					author: this.author,
					mobile: this.mobile,
					province: this.provinceId,
					city: this.cityId,
					district: this.districtId,
					address: this.address,
					tonglian_account: this.tonglian_account,
					email: this.email,
					// id_card: this.id_card,
					shop_intro: this.shop_intro
				}).then(res => {
					console.log(res);
					if (res.code == 10000) {
						uni.showToast({
							title: '提交成功',
							duration: 2000,
							success: function() {
								setTimeout(() => {
									uni.navigateBack();
								}, 2000);
							}
						});
					} else if (res.code == 10002) {
						uni.showToast({
							title: res.msg,
							duration: 2000,
							success: function() {
								setTimeout(() => {
									uni.navigateBack();
								}, 2000);
							}
						});
					}
				});
			} else if (this.condition === 0) {
				uni.showToast({
					title: '审核中不能修改',
					duration: 2000
				});
				return false;
			} else if (this.condition === 2) {
				uni.showToast({
					title: '审核通过不能修改',
					duration: 2000
				});
				return false;
			}
		}
	}
};
</script>

<style scoped lang="scss">
@import '../../assets/css/user/businessIn.scss';
</style>
